@mixin media-mobile {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@mixin media-tablet {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin media-desktop {
  @media (min-width: 1280px) {
    @content;
  }
}

